.footer {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #31343d;
    padding: 20px;
    position: fixed;
    width: 100%;
    bottom: 0;
  }
  
  .footer a {
    color: #92ccce;
    text-decoration: underline;
    margin-left: 20px;
    transition: all 0.3s ease-in-out;
  }
  
  .footer a:hover {
    color: #fff;
    background-color: #92ccce;
  }
  