.resume {
    background-color: #ffffff;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    margin: 10px;
    padding: 20px;
  }
  
  .title {
    color: #31343d;
    margin-bottom: 20px;
  }
  
  .link {
    display: block;
    margin-top: 10px;
    color: #31343d;
    text-decoration: none;
  }
  
  .proficiencies {
    list-style-type: none;
    padding: 0;
  }
  
  .proficiencies li {
    background-color: #92ccce;
    color: #fff;
    margin-bottom: 10px;
    padding: 5px;
    border-radius: 5px;
  }
  