.section {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
  }
  
  .title {
    color: #31343d;
    margin-bottom: 20px;
  }
  
  .project-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    margin-top: 20px;
  }
  
  .project-item {
    background-color: #ffffff;
    margin: 10px;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 300px;
    text-align: center;
  }
  
  .project-item img {
    width: 100%;
    height: auto;
    margin-bottom: 10px;
  }
  
  .project-item a {
    display: block;
    margin-top: 10px;
    color: #31343d;
    text-decoration: none;
  }
  